body {
    display: flex;
    main {
        width: 60%;

        .categories {
            margin-top: 2.5rem;
            ul {
                list-style-type: none;
                display: flex;
                align-items: center;
                gap: 2rem;
                a {
                    text-decoration: none;
                    font-weight: 500;
                    color: #000;
                }
                li:nth-child(2) a {
                    background: rgb(255, 200, 0);
                    color: #fff;
                    padding: .6em .8em;
                    border-radius: 12px;
                }
            }
        }

        .camping {
            display: flex;
            gap: 2em;
            margin-top: 2em;

            .camping-picture {
                width: 400px;
                height: 280px;
                background: {
                    image: url(../../Assets/EO.png);
                }
                border-radius: 18px;
            }

            .camping-description .view {
                div {
                    width: 8px;
                    aspect-ratio: 1;
                    border-radius: 50%;
                    margin-top: 5px;
                    background: rgb(59, 59, 210, );
                }
            } 
        }
    }

    .about {
        width: 40%;
    }
}